@import url('https://fonts.googleapis.com/css2?family=Barlow&family=Barlow+Condensed:wght@100;200;300;400;500&family=Bellefair&display=swap');


h1 {
  font-family: 'Bellefair', serif;
  font-size: 150px;
  color: #FFFFFF;
} 
h2 {
  font-family: 'Bellefair', serif;
  font-size: 100px;
  color: #FFFFFF;
}
h3 {
  font-family: 'Bellefair', serif;
  font-size: 56px;
  color: #FFFFFF;
}
h4 {
  font-family: 'Bellefair', serif;
  font-size: 32px;
  color: #FFFFFF;
}
h5 {
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 25px;
  letter-spacing: 4.75;
  color: #D0D6F9;
}
#subheading1 {
  font-family: 'Bellefair', serif;
  font-size: 28px;
  color: #FFFFFF;
}
#subheading2 {
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 14px;
  letter-spacing: 2.35;
  color: #FFFFFF;
}
#navText {
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 16px;
  letter-spacing: 2.7;
  color: #FFFFFF;
}
p {
  font-family: 'Bellefair', serif;
  color: #D0D6F9;
}
.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#HomeBkg {
  background-image: url('./static/home/background-home-desktop.jpg');
  min-height: 100vh;
  padding-top: 5%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100vw;
}
#HomeWrap {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 5%;
}
#HomeWrap p {
  text-align: left;
}
#HomeWrap h4 {
  color: #D0D6F9;
}
#CrewBkg {
  background-image: url('./static/crew/background-crew-desktop.jpg');
  min-height: 100vh;
  padding-top: 5%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100vw;
}
#TechBkg {
  background-image: url('./static/technology/background-technology-desktop.jpg');
  min-height: 100vh;
  padding-top: 5%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100vw;
}
#DestBkg {
  background-image: url('./static/destination/background-destination-desktop.jpg');
  min-height: 100vh;
  padding-top: 5%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100vw;
}
#navBarWrap {
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding-top: 2%;
}
.sticky {
  position: fixed;
  top: 0;
}
#navBarWrap img {
  align-self: center;
  padding-top: 15%;
}
#navBarLinks {
  background-blend-mode: lighten;
  backdrop-filter: blur(20px);
  width: 40%;
  height: 5em;
}
#navBarWrap ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
  padding-top: 2%;
}
#navBarWrap ul a {
  text-decoration: none;
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 16px;
  font-kerning: 2.7;
  color: #FFFFFF;
}
#navBarWrap ul a:Hover {
  border-bottom: 2px #ffffff solid;
  mix-blend-mode: normal;
}
#ExploreCircle {
  margin-top: 5%;
  background-color: #FFFFFF;
  padding: 2.5em;
  height: 100%;
  border-radius: 50%;
  display: flex;
  justify-content: space-around;
}
#ExploreCircle h4 a{
  color: black;
  text-decoration: none;
}
.DestWrap {
  display: flex;
  justify-content: space-around;
  width: 75%;
  margin-left: 12.5%;
}
#DestHeader {
  text-align: left;
  margin-left: 12%;
}
.DestWrap p {
  width: 50%;
  margin-left: 25%;
}
.DestInfo {
  display: flex;
  justify-content: space-evenly;
}
.DestNav ul{
  display: flex;
  justify-content: space-evenly;
  list-style-type: none;
  width: 50%;
  margin-left: 18%;
}
.DestNav ul li:hover {
  text-decoration: underline;
  cursor: pointer;
}
.CrewWrap {
  display: flex;
  justify-content: space-around;
  width: 75%;
  margin-left: 12.5%;
}
.TechWrap {
  display: flex;
  justify-content: space-around;
  width: 80%;
  margin-left: 10%;
}
.TechLeft {
  display: flex;
  width: 50%;
}
#darkText {
  color: #D0D6F9;
}
.carousel-button {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #FFFFFF;
  mix-blend-mode: normal;
  opacity: 0.17;
  margin-right: 24px;
}
.carousel-button:hover {
  background: #FFFFFF;
  mix-blend-mode: normal;
  opacity: 0.5;
}
.active-button {
  background: #FFFFFF;
  opacity: 1;
}
.active {
  border-bottom: 2px white solid;
  font-weight: bold;
}
.active:hover {
  border-bottom: 2px white solid;
  opacity: 1;
}
.technology-buttons {
  margin-right: 5.55vw;
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.technology-button {
  width: 80px;
  height: 80px;
  border: none;
  border-radius: 50%;
  background: transparent;
  mix-blend-mode: normal;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  font-family: Bellefair;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 37px;
  text-align: center;
  letter-spacing: 2px;
  color: #FFFFFF;
}
.technology-button:hover {
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  opacity: 0.25;
}
.active-technology {
  color: #0B0D17;
  background: #FFFFFF;
  opacity: 1;
}
.active-technology:hover {
  opacity: 1;
}
#crewLeft {
  width: 50%;
  overflow: hidden;
}
#crewRight {
  width: 50%;
}
#crewBtn {
  padding-top: 5%;
}
.Ham {
  display: none;
}
@media only screen and (max-width: 400px) {
  #HomeBkg {
    background-image: url('./static/home/background-home-mobile.jpg');
    padding-top: 5%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100vh;
  }
  #HomeWrap {
    flex-direction: column;
  }
  #CrewBkg {
    background-image: url('./static/crew/background-crew-mobile.jpg');
    padding-top: 5%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 110vh;
  }
  #DestHeader {
    margin-top: 18%;
  }
  .CrewWrap {
    flex-direction: column;
  }
  #crewLeft {
    width: 100%;
  }
  #crewRight img {
    width: 100%;
  }
  #crewRight {
    display: flex;
    align-self: center;
  }
  #TechBkg {
    background-image: url('./static/technology/background-technology-mobile.jpg');
    padding-top: 5%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100vh;
  }
  .TechWrap {
    flex-direction: column;
  }
  .technology-image {
    width:80%;
    align-self: center;
  }
  #DestBkg {
    background-image: url('./static/destination/background-destination-mobile.jpg');
    padding-top: 5%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100vh;
  }
  .DestWrap {
    flex-direction: column;
  }
  #DestImg img{
    width: 75%;
  }
  .DestNav {
    margin-top: 15%;
  }
  .DestNav ul{
    width: 80%;
    margin-left: 0%;
  }
  .DestWrap p {
    width: 80%;
    margin-left:10%;
  }
  #navBarLinks {
    display: none;
  }
  .Ham {
    margin-top: 5%;
    display: flex;
  }
  .navBar ul {
    display: flex;
    flex-direction: column;
  }
  .navBar button {
    position: fixed;
    z-index: 10;
  }
  .menuNav {
    overflow-y: scroll;
    list-style: none;
    position: fixed;
    top: 7.5%;
    right: 0;
    bottom: 0;
    height: 45vh;
    width: 0;
    overflow: hidden;
    max-width: 290px;
    z-index: 9;
    transition: .75s;
    border-top-left-radius: 2.5em;
    border-bottom-left-radius: 2.5em;
    background: rgba(255, 255, 255, 0.04);
    backdrop-filter: blur(81.5485px);
  }
  .menuNav.showMenu {
    width: 60%;
  }
  .menuNav li:first-child {
    margin-top: 3.5rem;
  }
  .navBar button {
    background: transparent;
    border: none;
  }
}